body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans';
  background-color: rgb(15,46,83);
}

a {
 outline: 0;
 text-decoration: none;
 color: rgb(244,176,4);
}

a:hover {
  text-decoration: none;
}

p.title {
  color: rgb(244,176,4);
  font-size: x-large;
  font-weight: 600;
}

p.subtitle {
  margin-top: -27px;
  font-size: smaller;
}

p.subtitle-large {
  margin-top: -15px;
  font-size: larger;
}

p.smaller {
  font-size: smaller;
}

div.flex-centered {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  text-align: justify;
}

div.flex-centered.content {
  margin-left: 10px;
  margin-right: 10px;
}

div.flex-row-centered {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.p-b {
  padding-bottom: 1rem;
}

.p-t {
  padding-top: 1rem;
}

div.h-spacer {
  margin-left: 5px;
  margin-right: 5px;
}

hr {
  width: 95%;
  border: 0;
  border-top: 1px solid rgba(244,176,4,0.7);
  height: 1px;
}



